import React, { Component } from 'react'
import {comment, uncomment} from './apiPost'
import {isAuthenticated} from '../auth'
import DefaultProfile from'../images/avatar.png'
import {Link} from 'react-router-dom'

class Comment extends Component {
   state = {
       text:"",
       error:""
   };
 handleChange = event => {
     this.setState({ error:""});
     this.setState({ text: event.target.value});
 };

 isValid =  () => {
     const {text} = this.state
     if(!text.length > 0 || text.length > 50){
         this.setState({error:"comment should not be empty and less than 50 "});
         return false 
     }
     return true
 }

 addComment = e => {
     e.preventDefault();

if(!isAuthenticated()){
    this.setState({ error:"please signin to leave a comment"});
    return false;
}

     if(this.isValid()) {

        const userId = isAuthenticated().user._id;
     const postId = this.props.postId;
     const token = isAuthenticated().token;
  

      comment(userId, token, postId ,{text: this.state.text })
        .then(data => {
            if(data.error){
            console.log(data.error)
        } else{
            this.setState({ text: ""});
            this.props.updateComments(data.comments);
        }
        })
     }
     
 };

 deleteComment = (comment) => {
    const userId = isAuthenticated().user._id;
    const token = isAuthenticated().token;
    const postId = this.props.postId;
 

    uncomment(userId, token, postId, comment)
       .then(data => {
           if(data.error){
           console.log(data.error);
       } else{
           this.props.updateComments(data.comments);
       }
       })
};

deleteConfirmed = (comment) => {
    let answer = window.confirm("Are you sure you want to delete your Comment??");
if(answer){
   this.deleteComment(comment);
}
};


    render() {

        
        const { comments } = this.props
        const {error} = this.state
       
        return (
            <div>
                
                <h2 className="mt-5 mb-5"> Leave a comment </h2>

                    <form onSubmit={this.addComment}>
                        <div className="form-group">
                        <input type="text" onChange={this.handleChange} 
                        value={this.state.text}
                        placeholder="Leave a comment..."
                        className="form-control"/>
                        <button className="btn btn-raised btn-success mt-2 " >Post</button>
                        </div>
                    </form>
                   
                    <div className="alert alert-primary"  style={{display:error ? "":"none"}}>
{error}

</div>
            <hr />
                <div className="col-md-12 ">
                       <h3 className="text-primary">{comments?.length}  Comments</h3>
                       <hr />
                       {comments?.map((comment, i ) => 
                        (
                         <div Key={i} >
                               
                                   <div>
                                  <Link to={`/user/${comment?.postedBy._id}`}>
                                        <img style={{ borderRadius: "50%" ,border:"1px solid black" }}
                                         className="foat-left mr-2" height="30px" width="30px"
                                        onError={i =>(i.target.src = `${DefaultProfile}`)}   
                                   src={`https://mern-insta-acc.herokuapp.com/user/photo/${comment?.postedBy._id}`} alt={comment.postedBy.name} /> 
                            
                            </Link>
                               
                               <div>
                                   <p className="lead">{comment?.text}</p>

                                   <br />
                                   <p className="font-italic mark">
                        posted By{" "}<Link to={`/user/${comment?.postedBy.name}`}>{comment?.postedBy.name} {" "}</Link>
                        on {" "}{new Date(comment?.created).toDateString()}
                  <span>

                  { isAuthenticated().user && 
                isAuthenticated().user._id === comment.postedBy._id &&
                             <>
                              
                            
                              <span onClick={() =>this.deleteConfirmed(comment)} className="text-info float-right mr-1 ">
                                Remove
                              </span>
                              </>
    }
                  </span>
                              </p>
                               </div>
                               
                               
                                       </div>
                                   
                                   </div>
                         )
                       )}
                   </div>
                
            </div>
        );
    }
}

export default Comment
