import React, { Component } from 'react'
import { singlePost , remove ,like, unlike} from './apiPost'
import{Link , Redirect } from 'react-router-dom';
import DefaultPost from'../images/avengers.jpg';
import {isAuthenticated} from '../auth'
import Comment from './Comment'

 class SinglePost extends Component {
            state = {
                post:"",
               redirectToHome: false,
               redirectToSignin: false,
               like: false,
               likes: 0,
              comments: []
            };

checkLike = likes =>{
    const userId = isAuthenticated() && isAuthenticated().user._id;
    let match = likes.indexOf(userId) !==-1;
    return match;
}

            componentDidMount = () => {
                const postId = this.props.match.params.postId
                singlePost(postId).then(data => {
                    console.log({ data })
                    if(data.error){
                        console.log(data.error)
                    }else{
                        this.setState({ 
                            post: data,
                            likes: data.likes.length,
                            like: this.checkLike(data.likes),
                            comments : data.comments
                         });
                    }
                });
            };  


            updateComments = comments => {
                this.setState( {comments} );
            }

 likeToggle = () => {

    if(!isAuthenticated()){
        this.setState({ redirectToSignin: true})
        return false;
    }
     let callApi = this.state.like ? unlike : like;
     const userId = isAuthenticated().user._id;
     const postId = this.state.post._id;
     const token = isAuthenticated().token;

     callApi(userId, token, postId ).then(data => {
         if(data.error) {
             console.log(data.error);
         }else {
             this.setState({
                 like: !this.state.like,
                 likes: data.likes.length,
                 like:this.checkLike(data.likes)
             });
         }
     });
 };


 
            deletePost = () => {
                const postId = this.props.match.params.postId
                const token = isAuthenticated().token
                remove(postId, token).then(data => {
                    if(data.error){
                        console.log(data.error)
                    }else{
                      this.setState({ redirectToHome:true});
                    }
                });
            };

            deleteConfirmed = () => {
                let answer = window.confirm("Are you sure you want to delete your account??");
           if(answer){
               this.deletePost();
           }
            };


    renderPost = (post) => {
        const posterId = post.postedBy
                         ? `/user/${post.postedBy._id}`
                         : "admin";
     const posterName = post.postedBy
                         ? post.postedBy.name
                         :" Unknown";
        
const {like, likes} = this.state;


                         return(
                  
                            <div className="card-body">
                                <img src={`https://mern-insta-acc.herokuapp.com/post/photo/${post._id}`}
                alt={post.title} 
                onError={i => i.target.src = `${DefaultPost}`}
                className="img-thimnail mb-3"
                style={{ height:'auto',width: "100%",objectFit:'cover'}}
                />

{like ? (

              <h3 onClick={this.likeToggle}><i className="fa fa-thumbs-up text-success bg-dark" style={{padding: '10px', borderRadius:'50%'}}/> {" "} {likes} like</h3>
) : ( 
    <h3 onClick={this.likeToggle}><i className="fa fa-thumbs-up text-success bg-dark" style={{padding: '10px', borderRadius:'50%'}}/> {" "} {likes} like</h3>
)}
                            
                              <p className="card-text">{post.body}</p>
                              <br />
                              <p className="font-italic mark">
                                  posted By { " "}<Link to={`${posterId}`}> { " "}{posterName}</Link> { " "}
                  on{new Date(post.created).toDateString()}
                
                              </p>
                              <div className="d-inline-block">
                              <Link 
                               to={`/`}
                               className="btn btn-raised  btn-primary btn-sm mr-5">
                                  Back to posts
                              </Link>

                             { isAuthenticated().user && 
                isAuthenticated().user._id === post.postedBy._id &&
                             <>
                              
                              <Link 
                               to={`/post/edit/${post._id}`}
                               className="btn btn-raised  btn-warning btn-sm mr-5">
                                  Update Post
                              </Link>
                              <button onClick={this.deleteConfirmed} className="btn btn-raised btn-info ">
                                   delete Post
                              </button>
                              </>
    }
                              </div>

                            </div> 
                          
                        )
    };


render() {

    const { post, redirectToHome , redirectToSignin , comments } = this.state;


    if(redirectToHome){
        return <Redirect to={`/`} />;
     }else if(redirectToSignin) {
        return <Redirect to={`/signin`} />;
     }

    return (
        <div className="container">

<div>
    {isAuthenticated().user &&
        isAuthenticated().user.role === "admin" && (
            <div class="card mt-5">
                <div className="card-body">
                    <h5 className="card-title">Admin</h5>
                 <p className="mb-2 text-danger">
                        Edit/Delete as an Admin
                        </p>
                   {/*} <Link
                        to={`/post/edit/${post._id}`}
                        className="btn btn-raised btn-warning btn-sm mr-5"
        >Update Post</Link>*/}
                    
                    <button
                        onClick={this.deleteConfirmed}
                        className="btn btn-raised btn-danger"
                    >
                        Delete Post
                        </button>
                </div>
            </div>
        )}
</div>

            <h2 className="display-2 mt-5 mb-5">{post.title} </h2>

            {!post ? (
                <div className="jumbotron text-center">
                    <h2> Loading...</h2>
                    </div>
            ): ( 
                this.renderPost(post)
             )}
<Comment postId={post._id} comments={comments.reverse()} updateComments= {this.updateComments} />
        </div>
    )
}
 };
export default SinglePost
